html {
  font-size: 10px;
  background: url(https://upload.wikimedia.org/wikipedia/commons/c/c5/Rolling_Stones_concert_in_Zuiderpark_te_Den_Haag_%2C_overzicht_Rolling_Stones_fans%2C_Bestanddeelnr_928-6118.jpg) bottom center;
  background-size: cover;
}

body,html {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
.container {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.nowPlaying {
  display: flex;
  align-items: center;
  justify-content: center;
  transform: scale(4.0);
}

.keys {
  display: flex;
  align-items: center;
  justify-content: center;
}

.key {
  border: .4rem solid black;
  border-radius: .5rem;
  margin: 1rem;
  font-size: 1.5rem;
  padding: 1rem .5rem;
  transition: all .07s ease;
  width: 10rem;
  text-align: center;
  color: white;
  background: rgba(0,0,0,0.8);
  text-shadow: 0 0 .5rem black;
}

:not(.nowPlaying) > .playing {
  transform: scale(1.2);
  border-color: #ffc600;
  box-shadow: 0 0 1rem #ffc600;
  background-color: rgba(255,255,255,0.9);
  color: #000;
  text-shadow: 0 0 0.1rem black;
}

kbd {
  display: block;
  font-size: 4rem;
}

.sound {
  font-size: 1.2rem;
  text-transform: uppercase;
  letter-spacing: .1rem;
  color: #ffc600;
}
